<script setup>
import { ref, computed, watch } from 'vue'
import { ElMessage } from 'element-plus'
import { Refresh, Edit } from '@element-plus/icons-vue'
import { templatesAPI } from '@/api'

const props = defineProps({
  modelValue: {
    type: Boolean,
    default: false
  },
  templateData: {
    type: Object,
    default: null
  }
})

const emit = defineEmits(['update:modelValue', 'edit'])

const visible = computed({
  get: () => props.modelValue,
  set: (val) => emit('update:modelValue', val)
})

const loading = ref(false)
const activeTab = ref('preview')

const previewData = ref({
  subject: '',
  content: '',
  used_vars: []
})

// ç¤ºä¾‹æ•°æ®
const sampleData = {
  Email: {
    Subject: 'ã€å‘Šè­¦ã€‘ç”Ÿäº§ç¯å¢ƒæ•°æ®åº“è¿æ¥å¼‚å¸¸',
    Sender: 'monitor@example.com',
    Content: 'ç”Ÿäº§ç¯å¢ƒæ•°æ®åº“è¿æ¥å‡ºç°å¼‚å¸¸ï¼Œè¿æ¥è¶…æ—¶ã€‚è¯·ç«‹å³æ£€æŸ¥æ•°æ®åº“æœåŠ¡å™¨çŠ¶æ€ã€‚é”™è¯¯ä¿¡æ¯ï¼šConnection timeout after 30 seconds',
    HTMLContent: '<p>ç”Ÿäº§ç¯å¢ƒæ•°æ®åº“è¿æ¥å‡ºç°å¼‚å¸¸ï¼Œè¿æ¥è¶…æ—¶ã€‚</p><p>è¯·ç«‹å³æ£€æŸ¥æ•°æ®åº“æœåŠ¡å™¨çŠ¶æ€ã€‚</p><p><strong>é”™è¯¯ä¿¡æ¯ï¼š</strong>Connection timeout after 30 seconds</p>',
    ReceivedAt: '2024-01-15 14:30:25',
    Size: 1024,
    MessageID: '<123456789@example.com>'
  },
  Alert: {
    ID: 1001,
    Subject: 'ã€å‘Šè­¦ã€‘ç”Ÿäº§ç¯å¢ƒæ•°æ®åº“è¿æ¥å¼‚å¸¸',
    Status: 'active',
    Content: 'ç”Ÿäº§ç¯å¢ƒæ•°æ®åº“è¿æ¥å‡ºç°å¼‚å¸¸ï¼Œè¿æ¥è¶…æ—¶ã€‚è¯·ç«‹å³æ£€æŸ¥æ•°æ®åº“æœåŠ¡å™¨çŠ¶æ€ã€‚',
    CreatedAt: '2024-01-15 14:30:25',
    ReceivedAt: '2024-01-15 14:30:25'
  },
  Rule: {
    ID: 1,
    Name: 'ç”Ÿäº§ç¯å¢ƒæ•°æ®åº“å‘Šè­¦',
    MatchType: 'keyword',
    MatchValue: 'æ•°æ®åº“,è¿æ¥,å¼‚å¸¸,è¶…æ—¶',
    Priority: 9,
    Description: 'ç”¨äºç›‘æ§ç”Ÿäº§ç¯å¢ƒæ•°æ®åº“ç›¸å…³å‘Šè­¦'
  },
  Mailbox: {
    ID: 1,
    Name: 'ç”Ÿäº§ç¯å¢ƒç›‘æ§é‚®ç®±',
    Email: 'monitor@example.com',
    Host: 'imap.example.com',
    Port: 993
  },
  System: {
    AppName: 'ç»Ÿä¸€é‚®ä»¶å‘Šè­¦å¹³å°',
    AppVersion: 'v1.0.0',
    ServerName: 'alert-server-01',
    Environment: 'production'
  },
  Time: {
    Now: Math.floor(Date.now() / 1000),
    NowFormat: new Date().toLocaleString('zh-CN'),
    Today: new Date().toLocaleDateString('zh-CN'),
    Year: new Date().getFullYear(),
    Month: String(new Date().getMonth() + 1).padStart(2, '0'),
    Day: String(new Date().getDate()).padStart(2, '0'),
    Hour: String(new Date().getHours()).padStart(2, '0'),
    Minute: String(new Date().getMinutes()).padStart(2, '0'),
    Second: String(new Date().getSeconds()).padStart(2, '0'),
    Weekday: ['æ˜ŸæœŸæ—¥', 'æ˜ŸæœŸä¸€', 'æ˜ŸæœŸäºŒ', 'æ˜ŸæœŸä¸‰', 'æ˜ŸæœŸå››', 'æ˜ŸæœŸäº”', 'æ˜ŸæœŸå…­'][new Date().getDay()]
  }
}

// è·å–æ¨¡ç‰ˆç±»å‹æ ‡ç­¾ç±»å‹
const getTypeTagType = (type) => {
  const typeMap = {
    email: 'primary',
    dingtalk: 'success',
    wechat: 'warning',
    markdown: 'info'
  }
  return typeMap[type] || 'info'
}

// è·å–æ¨¡ç‰ˆç±»å‹æ ‡ç­¾
const getTypeLabel = (type) => {
  const typeMap = {
    email: 'é‚®ä»¶æ¨¡ç‰ˆ',
    dingtalk: 'é’‰é’‰æ¨¡ç‰ˆ',
    wechat: 'ä¼ä¸šå¾®ä¿¡æ¨¡ç‰ˆ',
    markdown: 'Markdownæ¨¡ç‰ˆ'
  }
  return typeMap[type] || type
}

// æ ¼å¼åŒ–æ—¶é—´
const formatTime = (time) => {
  if (!time) return '-'
  return new Date(time).toLocaleString('zh-CN')
}

// æ¸²æŸ“é¢„è§ˆå†…å®¹
const renderPreviewContent = computed(() => {
  if (!previewData.value.content) return 'åŠ è½½ä¸­...'
  
  const content = previewData.value.content
  
  switch (props.templateData?.type) {
    case 'email':
      return content // HTML ç›´æ¥æ¸²æŸ“
    case 'dingtalk':
    case 'markdown':
      // ç®€å•çš„ Markdown æ¸²æŸ“
      return content
        .replace(/^### (.*$)/gm, '<h3>$1</h3>')
        .replace(/^## (.*$)/gm, '<h2>$1</h2>')
        .replace(/^# (.*$)/gm, '<h1>$1</h1>')
        .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
        .replace(/\*(.*?)\*/g, '<em>$1</em>')
        .replace(/^- (.*$)/gm, '<li>$1</li>')
        .replace(/^> (.*$)/gm, '<blockquote>$1</blockquote>')
        .replace(/`([^`]+)`/g, '<code>$1</code>')
        .replace(/\n/g, '<br>')
    case 'wechat':
    default:
      return content.replace(/\n/g, '<br>')
  }
})

// åˆ·æ–°é¢„è§ˆ
const refreshPreview = async () => {
  if (!props.templateData?.content) return
  
  loading.value = true
  try {
    const response = await templatesAPI.preview({
      content: props.templateData.content,
      subject: props.templateData.subject || ''
    })
    
    previewData.value = response.data
  } catch (error) {
    console.error('é¢„è§ˆå¤±è´¥:', error)
    ElMessage.error('é¢„è§ˆå¤±è´¥ï¼Œè¯·æ£€æŸ¥æ¨¡ç‰ˆè¯­æ³•')
  } finally {
    loading.value = false
  }
}

// ç¼–è¾‘æ¨¡ç‰ˆ
const handleEdit = () => {
  emit('edit', props.templateData)
  visible.value = false
}

// ç›‘å¬æ¨¡ç‰ˆæ•°æ®å˜åŒ–ï¼Œè‡ªåŠ¨åˆ·æ–°é¢„è§ˆ
watch(() => props.templateData, (newData) => {
  if (newData && visible.value) {
    refreshPreview()
  }
}, { immediate: true })

// ç›‘å¬å¯¹è¯æ¡†æ˜¾ç¤ºçŠ¶æ€
watch(visible, (show) => {
  if (show && props.templateData) {
    activeTab.value = 'preview'
    refreshPreview()
  }
})
</script>

