              <template #prefix>
                <el-icon><Search /></el-icon>
              </template>
            </el-input>
          </el-col>
          <el-col :span="6">
            <el-select
              v-model="searchForm.type"
              placeholder="æ¨¡ç‰ˆç±»å‹"
              clearable
              @change="handleSearch"
            >
              <el-option label="å…¨éƒ¨ç±»å‹" value="" />
              <el-option label="é‚®ä»¶æ¨¡ç‰ˆ" value="email" />
              <el-option label="é’‰é’‰æ¨¡ç‰ˆ" value="dingtalk" />
              <el-option label="ä¼ä¸šå¾®ä¿¡" value="wechat" />
              <el-option label="Markdown" value="markdown" />
            </el-select>
          </el-col>
          <el-col :span="6">
            <el-select
              v-model="searchForm.status"
              placeholder="çŠ¶æ€"
              clearable
              @change="handleSearch"
            >
              <el-option label="å…¨éƒ¨çŠ¶æ€" value="" />
              <el-option label="æ´»è·ƒ" value="active" />
              <el-option label="åœç”¨" value="inactive" />
            </el-select>
          </el-col>
          <el-col :span="4">
            <el-button @click="handleReset">é‡ç½®</el-button>
          </el-col>
        </el-row>
      </div>
      
      <!-- æ¨¡ç‰ˆåˆ—è¡¨ -->
      <div class="table-section">
        <el-table 
          :data="templateList" 
          v-loading="loading"
          stripe
          style="width: 100%"
        >
          <el-table-column prop="name" label="æ¨¡ç‰ˆåç§°" min-width="150">
            <template #default="scope">
              <div class="template-name">
                <span>{{ scope.row.name }}</span>
                <el-tag v-if="scope.row.is_default" type="success" size="small">é»˜è®¤</el-tag>
              </div>
            </template>
          </el-table-column>
          
          <el-table-column prop="type" label="æ¨¡ç‰ˆç±»å‹" width="120">
            <template #default="scope">
              <el-tag :type="getTypeTagType(scope.row.type)" size="small">
                {{ getTypeLabel(scope.row.type) }}
              </el-tag>
