          </el-table-column>
          
          <el-table-column prop="description" label="æè¿°" min-width="200" show-overflow-tooltip />
          
          <el-table-column prop="status" label="çŠ¶æ€" width="80">
            <template #default="scope">
              <el-tag :type="scope.row.status === 'active' ? 'success' : 'info'" size="small">
                {{ scope.row.status === 'active' ? 'æ´»è·ƒ' : 'åœç”¨' }}
              </el-tag>
            </template>
          </el-table-column>
          
          <el-table-column prop="updated_at" label="æ›´æ–°æ—¶é—´" width="180">
            <template #default="scope">
              {{ formatTime(scope.row.updated_at) }}
            </template>
          </el-table-column>
          
          <el-table-column label="æ“ä½œ" width="250" fixed="right">
