                  size="small" 
                  @click="handleEdit(scope.row)"
                  :icon="Edit"
                >
                  ç¼–è¾‘
                </el-button>
                <el-dropdown @command="(command) => handleDropdownCommand(command, scope.row)">
                  <el-button size="small" type="info">
                    æ›´å¤š<el-icon class="el-icon--right"><ArrowDown /></el-icon>
                  </el-button>
                  <template #dropdown>
                    <el-dropdown-menu>
                      <el-dropdown-item 
                        command="setDefault"
                        :disabled="scope.row.is_default"
                      >
                        è®¾ä¸ºé»˜è®¤
                      </el-dropdown-item>
                      <el-dropdown-item 
                        command="duplicate"
                      >
                        å¤åˆ¶æ¨¡ç‰ˆ
                      </el-dropdown-item>
                      <el-dropdown-item 
                        command="toggleStatus"
                        :class="scope.row.status === 'active' ? 'text-warning' : 'text-success'"
                      >
                        {{ scope.row.status === 'active' ? 'åœç”¨' : 'å¯ç”¨' }}
                      </el-dropdown-item>
                      <el-dropdown-item 
                        command="delete"
                        class="text-danger"
                        divided
                      >
                        åˆ é™¤
                      </el-dropdown-item>
                    </el-dropdown-menu>
                  </template>
                </el-dropdown>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>
        
        <!-- åˆ†é¡µ -->
        <div class="pagination-section">
          <el-pagination
            v-model:current-page="pagination.page"
            v-model:page-size="pagination.size"
            :page-sizes="[10, 20, 50, 100]"
            :total="pagination.total"
