<script setup>
import { ref, reactive, onMounted } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { Plus, Search, View, Edit, ArrowDown } from '@element-plus/icons-vue'
import { templatesAPI } from '@/api'
import TemplateForm from '@/components/TemplateForm.vue'
import TemplatePreview from '@/components/TemplatePreview.vue'

// å“åº”å¼æ•°æ®
const loading = ref(false)
const formVisible = ref(false)
const previewVisible = ref(false)
const templateList = ref([])
const currentTemplate = ref(null)

// æœç´¢è¡¨å•
const searchForm = reactive({
  name: '',
  type: '',
  status: ''
})

// åˆ†é¡µä¿¡æ¯
const pagination = reactive({
  page: 1,
  size: 20,
  total: 0
})

// è·å–æ¨¡ç‰ˆç±»å‹æ ‡ç­¾ç±»å‹
const getTypeTagType = (type) => {
  const typeMap = {
    email: 'primary',
    dingtalk: 'success',
    wechat: 'warning',
    markdown: 'info'
  }
  return typeMap[type] || 'info'
}

// è·å–æ¨¡ç‰ˆç±»å‹æ ‡ç­¾
const getTypeLabel = (type) => {
  const typeMap = {
    email: 'é‚®ä»¶',
    dingtalk: 'é’‰é’‰',
    wechat: 'å¾®ä¿¡',
    markdown: 'MD'
  }
  return typeMap[type] || type
}

// æ ¼å¼åŒ–æ—¶é—´
const formatTime = (time) => {
  if (!time) return '-'
  return new Date(time).toLocaleString('zh-CN')
}

// åŠ è½½æ¨¡ç‰ˆåˆ—è¡¨
const loadTemplateList = async () => {
  loading.value = true
  try {
    const params = {
      page: pagination.page,
      size: pagination.size,
      ...searchForm
    }
    
    // è¿‡æ»¤ç©ºå€¼
    Object.keys(params).forEach(key => {
      if (params[key] === '' || params[key] === null || params[key] === undefined) {
        delete params[key]
      }
    })
    
    const response = await templatesAPI.list(params)
    templateList.value = response.data.list || []
    pagination.total = response.data.total || 0
  } catch (error) {
    console.error('åŠ è½½æ¨¡ç‰ˆåˆ—è¡¨å¤±è´¥:', error)
    ElMessage.error('åŠ è½½æ¨¡ç‰ˆåˆ—è¡¨å¤±è´¥')
  } finally {
    loading.value = false
  }
}

// æœç´¢å¤„ç†
const handleSearch = () => {
  pagination.page = 1
  loadTemplateList()
}

// é‡ç½®æœç´¢
const handleReset = () => {
  Object.assign(searchForm, {
    name: '',
    type: '',
    status: ''
  })
  handleSearch()
}

// æ·»åŠ æ¨¡ç‰ˆ
const handleAdd = () => {
  currentTemplate.value = null
  formVisible.value = true
}

// ç¼–è¾‘æ¨¡ç‰ˆ
const handleEdit = (template) => {
  currentTemplate.value = { ...template }
  formVisible.value = true
}

// é¢„è§ˆæ¨¡ç‰ˆ
const handlePreview = (template) => {
  currentTemplate.value = template
  previewVisible.value = true
}

// ä¸‹æ‹‰èœå•å‘½ä»¤å¤„ç†
const handleDropdownCommand = async (command, template) => {
  switch (command) {
    case 'setDefault':
      await handleSetDefault(template)
      break
    case 'duplicate':
      await handleDuplicate(template)
      break
    case 'toggleStatus':
      await handleToggleStatus(template)
      break
    case 'delete':
      await handleDelete(template)
      break
  }
}

// è®¾ä¸ºé»˜è®¤æ¨¡ç‰ˆ
const handleSetDefault = async (template) => {
  try {
    await templatesAPI.setDefault(template.id)
    ElMessage.success('è®¾ç½®é»˜è®¤æ¨¡ç‰ˆæˆåŠŸ')
    loadTemplateList()
  } catch (error) {
    console.error('è®¾ç½®é»˜è®¤æ¨¡ç‰ˆå¤±è´¥:', error)
    ElMessage.error('è®¾ç½®é»˜è®¤æ¨¡ç‰ˆå¤±è´¥')
  }
}

// å¤åˆ¶æ¨¡ç‰ˆ
const handleDuplicate = async (template) => {
  try {
    const duplicateData = {
      name: `${template.name} - å‰¯æœ¬`,
      type: template.type,
      subject: template.subject,
      content: template.content,
      description: `${template.description || ''} (å¤åˆ¶è‡ª ${template.name})`,
      is_default: false,
      status: 'active'
    }
    
    await templatesAPI.create(duplicateData)
    ElMessage.success('å¤åˆ¶æ¨¡ç‰ˆæˆåŠŸ')
    loadTemplateList()
  } catch (error) {
    console.error('å¤åˆ¶æ¨¡ç‰ˆå¤±è´¥:', error)
    ElMessage.error('å¤åˆ¶æ¨¡ç‰ˆå¤±è´¥')
  }
}

// åˆ‡æ¢çŠ¶æ€
const handleToggleStatus = async (template) => {
  try {
    const newStatus = template.status === 'active' ? 'inactive' : 'active'
    await templatesAPI.update(template.id, { status: newStatus })
    ElMessage.success(`æ¨¡ç‰ˆå·²${newStatus === 'active' ? 'å¯ç”¨' : 'åœç”¨'}`)
    loadTemplateList()
  } catch (error) {
    console.error('åˆ‡æ¢çŠ¶æ€å¤±è´¥:', error)
    ElMessage.error('åˆ‡æ¢çŠ¶æ€å¤±è´¥')
  }
}

// åˆ é™¤æ¨¡ç‰ˆ
const handleDelete = async (template) => {
  try {
    await ElMessageBox.confirm(
      `ç¡®å®šè¦åˆ é™¤æ¨¡ç‰ˆ"${template.name}"å—ï¼Ÿæ­¤æ“ä½œä¸å¯æ¢å¤ã€‚`,
      'ç¡®è®¤åˆ é™¤',
      {
        confirmButtonText: 'ç¡®å®š',
        cancelButtonText: 'å–æ¶ˆ',
        type: 'warning'
      }
    )
    
    await templatesAPI.delete(template.id)
    ElMessage.success('åˆ é™¤æ¨¡ç‰ˆæˆåŠŸ')
    loadTemplateList()
  } catch (error) {
    if (error !== 'cancel') {
      console.error('åˆ é™¤æ¨¡ç‰ˆå¤±è´¥:', error)
      ElMessage.error('åˆ é™¤æ¨¡ç‰ˆå¤±è´¥')
    }
  }
}

// è¡¨å•æˆåŠŸå¤„ç†
const handleFormSuccess = () => {
  loadTemplateList()
}

// ä»é¢„è§ˆä¸­ç¼–è¾‘
const handleEditFromPreview = (template) => {
  currentTemplate.value = { ...template }
  previewVisible.value = false
  formVisible.value = true
}

// ç»„ä»¶æŒ‚è½½æ—¶åŠ è½½æ•°æ®
onMounted(() => {
  loadTemplateList()
})
</script>

